<template>
    <div>
        <div class="text-center mx-auto font-bold">
            <div class="mb-5">
                <h1 class="text-white text-5xl title">To Do List</h1>
            </div>
        </div>
        <div class="cardDivInner w-1/2 text-center mx-auto border border-zinc-500 rounded shadow-lg  font-bold p-5">
            <div class="mt-6">
                <div class="">
                    <input type="text" @keyup.enter="addTodo" class="focus:border-gray-500 focus:ring-0 focus:offset-0 addTodo text-gray-title border border-gray-500 rounded-lg text-left px-3 py-2 drop-shadow-2xl bg-transparent w-full" placeholder="+ Add To List">
                </div>

                <div>
                    <h1 class="text-white text-left my-5 text-lg">Tasks</h1>

                    <div v-for="(item, index) in todos" :key="item.id" class="transition ease-in-out delay-200 duraiton-200 flex align-middle">
                        <div id="checklist" @click="addCompleted(index)" class="w-1/2 text-white transition duration-200 ease-out text-1xl text-left my-5 transition ease-in-out delay-150   duration-200 cursor-pointer flex justify-start align-center">
                            <input v-bind:id="item.id" type="checkbox" name="checkbox"  class="outline-0 offset-0 ring-0 border-0 focus:outline-0 focus:ring-0 focus:offset-0 focus:border-0 focus:ring-offset-0"/>
                            <label v-bind:for="item.id">{{ item.text }}</label>
                        </div>

                        <span class="X-icon w-2/4 items-center justify-end inline-flex cursor-pointer" @click="removeTodo(index)">
                            <svg class="fill-current text-red-500" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z"/><path d="M0 0h24v24H0z" fill="none"/></svg>
                        </span>
                    </div>
                </div>

                <div>
                    <h1 class="text-white text-left my-5 text-lg">Completed</h1>

                    <div v-for="(item, index) in completed" :key="item.id" class="transition ease-in-out delay-200 duraiton-200 flex align-middle">
                        <div id="checklist"  class="w-1/2 text-white transition duration-200 ease-out text-1xl text-left my-5 transition ease-in-out delay-150   duration-200 cursor-pointer flex justify-start align-center">
                            <input v-bind:id="item.id" type="checkbox" name="checkbox"  class="outline-0 offset-0 ring-0 border-0 focus:outline-0 focus:ring-0 focus:offset-0 focus:border-0 focus:ring-offset-0"/>
                            <label v-bind:for="item.id">{{ item.text }}</label>
                        </div>

                        <span class="X-icon w-2/4 items-center justify-end inline-flex cursor-pointer" @click="removeCompleted(index)">
                            <svg class="fill-current text-red-500" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z"/><path d="M0 0h24v24H0z" fill="none"/></svg>
                        </span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'App',
        data() {
            return {
                todos: [
                    {
                        id: 1,
                        text: 'Learn Vue',
                        completed: false
                    },
                    {
                        id: 2,
                        text: 'Learn Vuex',
                        completed: false
                    },
                    {
                        id: 3,
                        text: 'Learn Vue Router',
                        completed: false
                    }
                ],
                completed: [
                    {
                        id: 4,
                        text: 'Learn Vue Router',
                        completed: true
                    }
                ],
            }
        },
        mounted() {
            this.getTodos();
        },
        methods: {
            getTodos() {
                if (localStorage.getItem('todos') && localStorage.getItem('completed')) {
                    this.todos = JSON.parse(localStorage.getItem('todos'));
                    this.completed = JSON.parse(localStorage.getItem('completed'));
                }
            },
            addTodo(e) {
                this.todos.push({
                    id: this.todos.length + 1,
                    text: e.target.value,
                    completed: false
                })
                e.target.value = '';
                this.saveTodos();
            },
            addCompleted(index) {
                this.todos[index].completed = true;
                this.completed.push(this.todos[index]);
                this.todos.splice(index, 1);
                this.saveTodos();
            },
            removeTodo(index) {
                this.todos.splice(index, 1);
                this.saveTodos();
            },
            removeCompleted(index) {
                this.completed.splice(index, 1);
                this.saveTodos();
            },
            saveTodos() {
                localStorage.setItem('todos', JSON.stringify(this.todos));
                localStorage.setItem('completed', JSON.stringify(this.completed));
            }
        },

    }
</script>

<style lang="scss"  scoped>
.title{
    font-family: 'Pacifico', cursive;
}
.text-gray-title{
    // color: #868e96;
    color: #a3bdbe;
}
.text-gray-title::placeholder {
    color: #a3bdbe;
}

#checklist {

    --check: #4F29F0;
    --disabled: #C3C8DE;
    --width: 100px;
    --height: 140px;
    --border-radius: 10px;
    position: relative;
    label {
        color: var(--text);
        position: relative;
        display: grid;
        align-items: center;
        transition: color .3s ease;
        &::before,
        &::after {
            content:"";
            position: absolute;
        }
        &::before {
            height: 2px;
            width: 8px;
            left: -27px;
            background: var(--check);
            border-radius: 2px;
            transition: background .3s ease; 
        }
        &:after {
            height: 4px;
            width: 4px;
            top: 8px;
            left: -25px;
            border-radius: 50%;
        }
    }
    input[type="checkbox"] {
        -webkit-appearance: none;
        -moz-appearance: none;
        position: relative;
        margin: 8px 15px 0 0;
        background: var(--background);
        &::before,
        &::after {
            content:"";
            position: absolute;
            height: 2px;
            top: 50%;
            background: var(--check);
            border-radius: 2px;
        }
        &::before {
            width: 0px;
            right: 60%;
            transform-origin: right bottom;
        }
        &::after {
            width: 0px;
            left: 40%;
            transform-origin: left bottom;
        }
        &:checked {
            &::before {
                animation: check-01 .4s ease forwards;
            }
            &::after {
                animation: check-02 .4s ease forwards;
            }
            + label {
                color: var(--disabled);
                animation: move .3s ease .1s forwards;
                &::before {
                    background: var(--disabled);
                    animation: slice .4s ease forwards;
                }
                &::after {
                    animation: firework .5s ease forwards .1s;
                }
            }
        }
    }
}

    @keyframes move {
    50% {
    padding-left: 8px;
    padding-right: 0px;
    }
    100% {
    padding-right: 4px;
    }
    }
    @keyframes slice {
    60% {
    width: 100%;
    left: 4px;
    }
    100% {
    width: 100%;
    left: -2px;
    padding-left: 0;
    }
    }
    @keyframes check-01 {
    0% {
    width: 4px;
    top: auto;
    transform: rotate(0);
    }
    50% {
    width: 0px;
    top: auto;
    transform: rotate(0);
    }
    51% {
    width: 0px;
    top: 4px;
    transform: rotate(45deg);
    }
    100% {
    width: 5px;
    top: 4px;
    transform: rotate(45deg);
    }
    }
    @keyframes check-02 {
    0% {
    width: 4px;
    top: auto;
    transform: rotate(0);
    }
    50% {
    width: 0px;
    top: auto;
    transform: rotate(0);
    }
    51% {
    width: 0px;
    top: 4px;
    transform: rotate(-45deg);
    }
    100% {
    width: 10px;
    top: 4px;
    transform: rotate(-45deg);
    }
    }
    @keyframes firework {
    0% {
    opacity: 1;
    box-shadow: 0 0 0 -2px #4F29F0, 0 0 0 -2px #4F29F0, 0 0 0 -2px #4F29F0, 0 0 0 -2px #4F29F0, 0 0 0 -2px #4F29F0, 0 0 0 -2px #4F29F0;
    }
    30% {
    opacity: 1;
    }
    100% {
    opacity: 0;
    box-shadow: 0 -15px 0 0px #4F29F0, 14px -8px 0 0px #4F29F0, 14px 8px 0 0px #4F29F0, 0 15px 0 0px #4F29F0, -14px 8px 0 0px #4F29F0, -14px -8px 0 0px #4F29F0;
    }
}

</style>